header{
    grid-area: header;
    display: grid;
    grid-template-columns: 1fr; 
    grid-template-rows: 1fr;
    grid-template-areas: 
    "home buttons";
    margin: 10px 50px;
    
}


header button{
    font-family: 'Spartan', sans-serif;
    font-weight: bold;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: white;
    background-color: Transparent;
    justify-self: end;
    border: none;
    align-self: center;  
    
}


.buttonh{
    grid-row: buttons;
    display: grid;
    grid-template-columns: repeat(8,1fr);
}


.home{
    font-family: 'Antonio', sans-serif;
    justify-self: start;
    font-size: 28px;
    grid-area: home;
}

@media (max-width: 1200px){
    header{
        grid-area: header;
        display: grid;
        grid-template-columns: 1fr; 
        grid-template-rows: 1fr 1fr;
        grid-template-areas: 
        "home "
        "buttons";
    }

    .home{
        font-family: 'Antonio', sans-serif;
        justify-self:center;
        font-size: 28px;
    }

    .buttonh{
        grid-row: buttons;
        display: grid;
        grid-template-columns: repeat(8,1fr);
       

    }
    
}

