@import url(https://fonts.googleapis.com/css2?family=Antonio&family=Spartan&display=swap);

.body{
  font-family: 'Spartan', sans-serif;
  background-color: #070724;
}

.info{
    display: grid;
    grid-template-columns: repeat(1,350px);
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
    "planet-info"
    "buttons";
    grid-area: info;
    grid-gap: 50px;
    gap: 50px;
    justify-self: center;
}

.middle{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
    "img img info info"
    "data data data data";
    grid-area: planet-summary;
}


.title{
    grid-area: title;
    font-family: Antonio;
    font-style: normal;
    font-weight: normal;
    font-size: 80px;
    line-height: 104px;
    text-transform: uppercase;    
    color: #FFFFFF;
    min-width: 350px;
}

.summary{
    grid-area: summary;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    color: #FFFFFF;
   
    
}

.buttons{
    grid-area: buttons;
    display: grid;
    align-self: center;
    grid-row-gap:  20px;
    row-gap:  20px;
}


.info button{
    width: 350px;
    height: 48px;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 25px;
    letter-spacing: 2.57143px;
    text-transform: uppercase;
    color: #FFFFFF;
    border: 1px solid rgba(255,255, 255, .5);
    box-sizing: border-box;
    justify-items: end;
    background-color: Transparent;
}

.info button:hover{
    background-color: #38384F;
}
.planet-img{
    grid-area: img;
    align-self: center;
    justify-self: end;
    position: relative;
}

.zoom {
    position: absolute;
    width: 163px;
    height: 199px;
    top: 80%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    display: none;
}


.info .Mercury{
    background-color:  #419EBB;
}


.stats{
    grid-area: data;
    display: grid;
    grid-template-columns:repeat(4,255px);
    -webkit-align-items: center;
            align-items: center;
    justify-self: center;
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
}

.data_title{
    font-style: normal;
    font-weight: bold;
    font-size: 11px;
    line-height: 25px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #FFFFFF;
    mix-blend-mode: normal;
    opacity: 0.5; 

}

.stat{
    border: 1px solid rgba(255,255, 255, .5);
    box-sizing: border-box;
    padding: 10px;
}

.main-info {
    font-family: 'Antonio', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 52px;
    letter-spacing: -1.5px;
    text-transform: uppercase;
    color: #FFFFFF;  
}



.info .Venus{
    background-color: #EDA249;
 }
 
 .info .Earth{
     background-color:   #6D2ED5;
 }
 
 
 .info .Mars{
     background-color:#D14C32;
 }
 
 .info .Jupiter{
     background-color:  #D83A34;
 }
 
 .info .Saturn{
   background-color:  #CD5120;
 }
 
 .info .Uranus{
    background-color:  #1EC1A2;
 }
 
 .info .Neptune{
     background-color:  #2D68F0;
 }
 
 @media (max-width: 1200px){

    .info{
        display: grid;
        grid-template-columns: repeat(1,350px);
        grid-template-rows: 1fr 1fr;
        grid-template-areas:
        "planet-info buttons";
        grid-area: info;
        grid-gap: 50px;
        gap: 50px;
        justify-self: center;
    }
    
    .middle{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr 300px 178px;
        grid-template-areas: 
        "img img img img"
        "info info info info"
        "data data data data";
        grid-area: planet-summary;
    }

    .planet-img{
       justify-self: center;
    }
    .buttons{
        grid-area: buttons;
        display: grid;
        align-self: end;
        grid-row-gap:  20px;
        row-gap:  20px;
    }
    
    .stats{
        grid-area: data;
        display: grid;
        grid-template-columns:repeat(4,178px);
        -webkit-align-items: center;
                align-items: center;
        justify-self: center;
        grid-column-gap: 30px;
        -webkit-column-gap: 30px;
                column-gap: 30px;
    }
}

.Mercury{
    border-bottom: 1px solid #419EBB;
    padding-bottom: 3px;
}


.Venus{
    padding-bottom: 3px;
    border-bottom: 1px solid #EDA249;
}

.Earth{
    padding-bottom: 3px;
    border-bottom: 1px solid  #6D2ED5;
}


.Mars{
    padding-bottom: 3px;
    border-bottom: 1px solid  #D14C32;
}

.Jupiter{
    padding-bottom: 3px;
    border-bottom: 1px solid  #D83A34;
}

.Saturn{
    padding-bottom: 3px;
    border-bottom: 1px solid  #CD5120;
}

.Uranus{
    padding-bottom: 3px;
    border-bottom: 1px solid #1EC1A2;
}

.Neptune{
    padding-bottom: 3px;
    border-bottom: 1px solid #2D68F0;
}

header{
    grid-area: header;
    display: grid;
    grid-template-columns: 1fr; 
    grid-template-rows: 1fr;
    grid-template-areas: 
    "home buttons";
    margin: 10px 50px;
    
}


header button{
    font-family: 'Spartan', sans-serif;
    font-weight: bold;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: white;
    background-color: Transparent;
    justify-self: end;
    border: none;
    -webkit-align-self: center;
            align-self: center;  
    
}


.buttonh{
    grid-row: buttons;
    display: grid;
    grid-template-columns: repeat(8,1fr);
}


.home{
    font-family: 'Antonio', sans-serif;
    justify-self: start;
    font-size: 28px;
    grid-area: home;
}

@media (max-width: 1200px){
    header{
        grid-area: header;
        display: grid;
        grid-template-columns: 1fr; 
        grid-template-rows: 1fr 1fr;
        grid-template-areas: 
        "home "
        "buttons";
    }

    .home{
        font-family: 'Antonio', sans-serif;
        justify-self:center;
        font-size: 28px;
    }

    .buttonh{
        grid-row: buttons;
        display: grid;
        grid-template-columns: repeat(8,1fr);
       

    }
    
}


